.gbutton{
    position: relative;
}
.gbutton::before{
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: #233356;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    border-radius: inherit;
}
.gbutton__wrapper{
    width: 100%;
    height: 100%;
    border: 3px solid #233356;
    background: inherit;
    border-radius: inherit;
    position: relative;
    z-index: 2;    
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;

    transform: translate(-5px, -8px);
    transition: transform .4s ease;
}
.gbutton__text{
    font-family: "GumboDEMO", sans-serif;
    font-size: 36px;
    font-style: normal;
    color: #FFFFFF;
    position: relative;
    z-index: 2;
}
.gbutton__icon{
    max-width: 36px;
    max-height: 36px;
    width: 100%;
    margin-right: 14px;
    position: relative;
    z-index: 2;
}
.gbutton__glass{
    position: absolute;
    top: 0;
    left: 102px;
    height: 100%;
    z-index: 1;

    transition: left .4s ease;
}

.gbutton:hover{
    .gbutton__wrapper{
        transform: translate(-2px, -4px);
    }
    .gbutton__glass{
        left: 65%;
    }
}