.title{
    position: relative;
}
.title span{
    font-family: "GumboDEMO", sans-serif;
    font-size: 88px;
    line-height: 1.227;

    letter-spacing: 0.03em;
    color: #FFFFF3;

    -webkit-text-stroke: 0.5px #090065;
    /* shadow main */

    text-shadow: 5px 6px 0px #090065;
}
.title img{
    width: 47px;
    height: 47px;
    left: 97%;
    bottom: 75%;
    position: absolute;
}
