.community{
    position: relative;
    z-index: 3;
    
    padding-top: 11.666vw;
    padding-bottom: 13.402vw;

    @media (min-width: 1440px){        
        padding-top: 168px;
        padding-bottom: 193px;
    }
}
.community__background{
    width: 100%;
    max-height: 870px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
}
.community__fishes-blur{
    position: absolute;
    left: 50%;
    top: 4.652vw;
    transform: translateX(-50%);
    z-index: 2;
    max-width: 422px;
    width: 29.305vw;

    @media (min-width: 1440px){
        top: 67px;
    }
}
.community__container{
    position: relative;
    z-index: 4;
}
.community__content{
    margin: 0 auto;
    max-width: 641px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.community__title{
    max-width: 31.319vw;
    text-align: center;
    line-height: 1.22;

    span{
        font-size: 6.111vw;
    }

    @media (min-width: 1440px){
        max-width: 451px;
        span{
            font-size: 88px;
        }        
    }
}
.community__controls{
    margin-top: 3.75vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: 1440px){
        margin-top: 54px;        
    }
}
.community__btn{
    width: 21.25vw;
    height: 5.14vw;
    border-radius: 1.18vw;

    .gbutton__icon{
        max-width: 2.5vw;
        max-height: 2.5vw;
        margin-right: 0.972vw;
    }
    .gbutton__text{
        font-size: 2.5vw;
    }
    
    &_light{
        background: #36B9FF;
    }
    &_dark{
        background: #5662F6;
    }

    @media (min-width: 1440px){
        width: 306px;
        height: 72px;    
        border-radius: 17px;
        
        .gbutton__icon{
            max-width: 36px;
            max-height: 36px;
            margin-right: 14px;
        }
        .gbutton__text{
            font-size: 36px;
        }
    }
}
.community__btn + .community__btn{
    margin-top: 1.5277vw;

    @media (min-width: 1440px){
        margin-top: 22px;        
    }
}