@mixin fontCreate($name, $weight, $url, $style: normal) {
    @font-face {
        font-family: $name;
        font-weight: $weight;
        font-style: $style;
        src: url("#{$url}.woff2") format("woff2"),
             url("#{$url}.woff") format("woff"),
             url("#{$url}.otf") format("opentype"),
             url("#{$url}.eot") format("embedded-opentype");
    }
}

@function calcSize($size, $per){
    @return $size * ($per / 100);
}

@mixin FuroreReg{
    font-family: "Furore", sans-serif;
    font-weight: 400;
}
@mixin PoppinsReg {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
}
@mixin PoppinsMed {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
}
@mixin MontserratMed {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
}