.header-nav{
    display: flex;
    align-items: center;
    max-width: 457px;
    width: 100%;
    padding: 0 10px;
}

// Link
.header-nav__link{
    display: flex;
    align-items: center;

    img{
        max-width: 18px;
        max-height: 18px;
    }
    span{
        margin-left: 7px;
        white-space: nowrap;
        font-size: 18px;
        color: #FFFFF3;
        text-shadow: 
            1.5px 1.5px 0px #000000;
        -webkit-text-stroke: 0.3px #090065;
    }
}
.header-nav__margined{
    flex: 1 0 10px;
    max-width: 42px;
}