.team{
    padding-top: 23.819vw;
    padding-bottom: 8.68vw;
    position: relative;
    z-index: 4;
    overflow: hidden;

    @media (min-width: 1440px){
        padding-top: 343px;
        padding-bottom: 125px;
    }
}
.team__ref-left,
.team__ref-right{
    position: absolute;
    z-index: 2;
    top: 0;
}
.team__ref-left{
    max-width: 494px;
    width: 34.305vw;
    left: 0;
}
.team__ref-right{
    max-width: 276px;
    width: 19.166vw;
    right: 0;
}
.team__fishes-blur{
    position: absolute;
    left: 50%;
    top: 7.77vw;
    transform: translateX(-50%);
    z-index: 1;
    max-width: 422px;
    width: 29.3vw;

    @media (min-width: 1440px){
        top: 112px;
    }
}

.team__swiper{
    width: 100vw;
    max-width: 1428px;
    margin-left: 15.2vw;
    position: relative;
    z-index: 3;
    margin-top: 76px!important;
    overflow: visible;

    @media (min-width: 1440px){
        margin: auto;
    }
}
.team__title{
    display: inline-block;
    text-transform: uppercase;
    z-index: 3;
    margin-left: 24.01vw;
}
.team__item{
    padding: 0 15px;
}
.team__wrapper{
    display: flex;
    flex-direction: column;
}
.team__image{
    border: 3px solid #000000;
    box-shadow: 6px 6px 0px #000000;
    border-radius: 4.652vw;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;

    img{
        display: block;
        width: calc(100% + 1px);
        height: 100%;
    }

    @media (min-width: 1440px){
        border-radius: 67px;
    }
}
.team__name,
.team__prof{
    font-family: "PlusJakartaText", sans-serif;
    line-height: 1.29;
    letter-spacing: 0.03em;    
    color: #FFFFFF;    
    text-shadow: 0px 4px 92px rgba(0, 0, 0, 0.75);
}
.team__name{
    margin-top: 2.08vw;
    font-weight: bold;
    font-size: 1.9vw;

    @media (min-width: 1440px){
        margin-top: 30px;
        font-size: 28px;
    }
}
.team__prof{
    margin-top: 6px;
    font-weight: 400;
    font-size: 1.73vw;

    @media (min-width: 1440px){
        font-size: 25px;
    }
}