.roadmap{
    padding-top: 24.8vw;
    padding-bottom: 7vw;
    position: relative;
    z-index: 3;

    @media (min-width: 1440px){
        padding-top: 357px;
        padding-bottom: 100px;
    }
}
.roadmap__fishes-blur{
    max-width: 422px;
    width: 29.3vw;
    position: absolute;

    top: 1.8vw;
    right: 11vw;
}
.roadmap__reef{
    width: 47.91vw;
    max-width: 691px;
    max-height: 575px;
    position: absolute;
    z-index: 2;

    &_01,
    &_03{
        right: 0;
    }

    &_02,
    &_04{
        left: 0;
        transform: scaleX(-1);
    }

    &_01{
        top: 17.013vw;
    }
    &_02{
        top: 62.5vw;
    }
    &_03{
        top: 105.41vw;
    }
    &_04{
        top: 145.13vw;
    }    

    @media (min-width: 1440px){
        &_01{
            top: 245px;
        }
        &_02{
            top: 900px;
        }
        &_03{
            top: 1518px;
        }
        &_04{
            top: 2090px;
        }        
    }
}
.roadmap__bubles{
    position: absolute;
    left: 50%;
    bottom: 87%;
    transform: translateX(-50%);

    width: 97vw;
    max-width: 1440px;
    height: auto;
    z-index: 1;
}
.roadmap__container{
    width: 87.847vw!important;

    @media (min-width: 1440px){
        max-width: 1265px;        
    }
}
.roadmap__title{
    text-transform: uppercase;
    display: inline-block;
    position: relative;
    z-index: 3;

    span{        
        font-size: 6.111vw;
    }
    
    @media (min-width: 1440px){
        span{            
            font-size: 88px;
        }
    }
}

// Slider
.roadmap__slider{
    padding-top: 21.388vw;
    position: relative;
    z-index: 3;
    max-width: 100%;
    margin: 0 auto;
    flex-direction: column;
    overflow: visible;

    .swiper-slide{
        height: auto!important;
    }

    .roadmap-item{
        display: flex;
        
        &_first{
            justify-content: flex-end;
        }
        &_second{
            padding-top: 7.777vw;
        }
        &_third{
            justify-content: flex-end;
            margin-right: 5.76vw;
            padding-top: 2.5vw;
        }

        @media (min-width: 1440px){
            &_first{
                justify-content: flex-end;
            }
            &_second{
                padding-top: 7.777vw;
            }
            &_third{
                justify-content: flex-end;
                margin-right: 5.76vw;
                padding-top: 2.5vw;
            }
        }
    }

    @media (min-width: 1440px){
        padding-top: 308px;  

        .roadmap-item{
    
            &_second{
                padding-top: 112px;
            }
            &_third{
                margin-right: 83px;
                padding-top: 36px;
            }
        }
    }
}

.roadmap-item__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.roadmap-item__title{   
    display: inline-block;     

    span{
        font-size: 5.27vw;
        position: relative;
        z-index: 2;
        
        @media (min-width: 1440px){
            font-size: 76px;            
        }
    }
}
.roadmap-item__title-bubble{
    width: 8.88vw;
    height: 8.125vw;
    fill: #406CB1;
    z-index: 1; 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
        
    @media (min-width: 1440px){
        width: 128px;
        height: 117px;        
    }
}
.roadmap-item__paper{
    margin-top: 3.05vw;
    max-width: 29.3vw;
    padding: 3.33vw 2.42vw 4.0277vw;

    background: #FFFFFF;
    border: 0.2vw solid #000000;
    box-shadow: 0.4vw 0.55vw 0px #000000;
    border-radius: 0.9722vw;
        
    @media (min-width: 1440px){
        margin-top: 44px;
        max-width: 422px;
        padding: 48px 34px 58px;
        border-radius: 14px;       
        border: 3px solid #000000;
        box-shadow: 6px 8px 0px #000000; 
    }
}
.roadmap-item__point{
    display: flex;
    align-items: flex-start;

    img{
        flex-shrink: 0;
        width: 21px;
        height: 23px;
    }
    span{
        margin-left: 1.18vw;
        font-family: "PlusJakartaText", sans-serif;
        font-weight: 700;
        font-size: 1.388vw;
        line-height: 1.35;
        color: #233356;
    }
        
    @media (min-width: 1440px){
        span{
            font-size: 20px;
            margin-left: 17px;
        }
    }
}
.roadmap-item__point + .roadmap-item__point{
    margin-top: 1.52777vw;

    @media (min-width: 1440px){
        margin-top: 22px;
    }
}
.roadmap-item__path1{
    position: absolute;
    right: 95%;
    bottom: 85%;
    max-width: 783px;
    width: 54.375vw;
}
.roadmap-item__path2{
    position: absolute;
    left: 105%;
    bottom: 70%;
    max-width: 312px;
    width: 21.666vw;
}
.roadmap-item__path3{
    position: absolute;
    right: 90%;
    bottom: 95%;
    max-width: 266px;
    width: 18.472vw;
}