.whalies{
    padding-top: 11.45vw; 
    padding-bottom: 12.5vw;
    position: relative;
    z-index: 3;

    @media (min-width: 1440px){
        padding: 165px 0 180px;
    }
}
.whalies__container{
    max-width: 1132px;
    position: relative;
    z-index: 3;
}
.whalies{

}
.whalies__title{
    text-align: center;

    span{        
        font-size: 88px;
        text-shadow: 5px 6px 0px #090065;
    }

    @media (max-width: 1075px){
        span{        
            font-size: 7.32vw;
            text-shadow: 0.408vw 0.49vw 0px #090065;
        }
    }
    @media (max-width: 768px){
        span{
            font-size: 12.07vw;
            line-height: 1.25;
            text-shadow: 3px 4px 0px #233356;
        }
    }
    @media (max-width: 414px){
        font-size: 38px;
    }
}
.whalies__title .fishes{
    max-width: 341px;
    width: 23.68vw;
    height: auto;

    position: absolute;
    left: 85%;
    bottom: 30%;

    @media (max-width: 769px){
        width: 192px;
        bottom: 50%;
        left: 75%;
    }
    @media (max-width: 359px){
        bottom: 40%;
        left: 67%;
    }
}
.swiper-slide{
    height: auto;
}
.whalies__block{    
    overflow-x: hidden;
}
.whalies__slider{
    width: 100%;
    margin-top: 4.23vw;

    .swiper{        
        overflow: visible;
    }

    @media (min-width: 1440px){
        margin-top: 61px;
    }
    @media (max-width: 565px){
        width: 108vw;
        margin-left: -25vw;
    }
}
.whalies__item{
    padding: 0 26px;
    height: 100%;

    @media (max-width: 991px){
        padding: 0 15px;
    }
    @media (max-width: 434px){
        padding: 0 5px;
    }
}
.whalies__wrapper{
    height: 100%;
    display: flex;
    align-items: flex-end;
    position: relative;

    img{
        margin-top: auto;
        width: 100%;
    }
}
.whalies__text{
    position: absolute;
    left: 31px;
    bottom: 25px;
    font-size: 34px;   
    line-height: 1.24;

    letter-spacing: 0.03em;

    color: #606060;

    @media (max-width: 665px){
        left: 4.66vw;
        bottom: 3.76vw;
        font-size: 5.11vw;   
    }
    @media (max-width: 441px){
        left: 21px;
        bottom: 17px;
        font-size: 23px;
    }
    @media (max-width: 359px){
        left: 17px;
        bottom: 13px;
        font-size: 20px;
    }
}

// reefs
.whalies-reef-left,
.whalies-reef-right{
    position: absolute;
    z-index: 2;
}
.whalies-reef-left{
    left: 0;
    top: 0;
    max-width: 154px;
    width: 10.7vw;

    @media (max-width: 768px){
        display: none;
    }
}
.whalies-reef-right{
    right: 0;
    bottom: 0;
    max-width: 130px;
    width: 9.2vw;

    @media (max-width: 768px){
        right: auto;
        left: 0;
        width: 17.4vw;
        top: 95%;
        max-width: 72px;
    }
}