.intro{
    position: relative;
}
.intro__background{
    margin-top: -75px;
    width: 100vw;
    max-width: 100%;
    position: relative;
    z-index: 3;

    .back{
        max-width: 100%;
        max-height: 940px;
        display: block;
        width: 100%;
        position: relative;
        z-index: 2;

        
    }
    .palm-left{
        max-width: 335px;
        position: absolute;
        z-index: 3;
        left: 0;
        top: 5%;
        width: 23.19vw;

        @media (max-width: 665px){
            top: -5%;
            width: 42vw;
        }
    }
    .palm-right{
        max-width: 287px;
        position: absolute;
        right: 0;
        top: 0;
        width: 19.9vw;   
        z-index: 3;
        transform: scaleX(-1);

        @media (max-width: 665px){
            width: 26.57vw;
        }
    }
    .palm-blur{
        max-width: 542px;
        position: absolute;
        right: 0;
        top: 17%;
        width: 37.63vw;   
        z-index: 4;    

        @media (max-width: 768px){
            display: none;
        }
    }
    .sun{
        max-width: 991px;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 68.81vw;
        z-index: 1;

        @media (max-width: 665px){
            width: 47.58vw;
            top: 7%;
        }
    }
    @media (max-width: 1221px){
        margin-top: -20px;
    }
    @media (max-width: 991px){
        margin-top: 25px;
    }
    @media (max-width: 665px){
        margin-top: 0;
    }
}

.intro__content{
    position: absolute;
    left: 50%;
    top: 123px;
    z-index: 10;
    transform: translateX(-50%);
    max-width: 689px;
    text-align: center;

    @media (max-width: 1221px){
        top: 3.819vw;
    }
    @media (max-width: 768px){
        max-width: 375px;
        width: 100%;
        top: 0;
    }
    @media (max-width: 665px){
        display: none;
    }
}
.intro__content h1{
    font-size: 4.93vw;
    line-height: 1.225;
    -webkit-text-stroke: 1px #090065;
    color: #fff;

    text-shadow: 5px 6px 0px #090065;

    @media (min-width: 1440px){
        font-size: 71px;
    }

    @media (max-width: 991px){
        font-size: 44px;
        text-shadow: 3px 4px 0px #090065;
        -webkit-text-stroke: 0.4px #090065;
    }
}
.intro__content p{
    font-family: "HeliosExtC", sans-serif;
    font-weight: 700;
    font-size: 1.1vw;
    line-height: 1.9375;

    color: #000000;

    @media (min-width: 1440px){
        font-size: 16px;
    }
    @media (max-width: 991px){
        font-size: 13px;
        line-height: 1.5;
        margin-top: 0;
    }
}
.intro__btn{
    margin: 3.4vw auto 0;

    width: 21.25vw;
    height: 5vw;
    background: #81CBEA;
    border-radius: 1.18vw;

    .gbutton__wrapper{
        border: 3px solid #233356;
        transform: translate(-0.34vw, -0.55vw);
    }
    .gbutton__glass{
        left: 4.1vw;
    }
    .gbutton__text{
        font-size: 2.5vw;
    }

    @media (min-width: 1440px){
        width: 306px;
        height: 72px;
        border-radius: 17px;
        margin-top: 44px;  

        .gbutton__wrapper{      
            transform: translate(-5px, -8px);
        }
        .gbutton__text{
            font-size: 36px;
        }
    }
    @media (max-width: 991px){
        width: 184px;
        height: 43px;
    }
}

// Dolphins
.intro__dolphins{
    width: 71.38vw;
    max-width: 1028px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 0;
    transform: translateX(-60%);

    .dolphin-left{
        max-width: 302px;
        width: 100%;
    }
    .dolphin-middle{
        max-width: 387px;
        width: 100%;
        transform: translateY(50px);
    }
    .dolphin-right{
        max-width: 273px;
        width: 100%;
    }

    @media (max-width: 768px){
        transform: translateX(-50%);
    }
    @media (max-width: 665px){
        width: 90%;
        bottom: 15%;

        .dolphin-left{
        }
        .dolphin-middle{
            padding-top: 8.9vw;
        }
        .dolphin-right{
            padding-bottom: 10.38vw;
        }
    }
}