@import "reset.scss";
@import "mixins.scss";
@import "vars.scss";
@import "fonts.scss";

*,
*::after,
*::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    font-family: "GumboDEMO", sans-serif;
    font-weight: 700;
}
.container{
    width: 100%;
    margin: 0 auto;
    padding: 0 40px;

    @media (max-width: 991px){
        padding: 0 25px;
    }
    @media (max-width: 434px){
        padding: 0 15px;
    }
}
.page{
    min-height: 100vh;
    background: linear-gradient(180deg, #92CCEC 0%, #3B64BE 100%);

    @media (max-width: 768px){
        background: linear-gradient(180deg, #99C0FF 18.23%, #363D9F 100%);
    }
}
.page__lights{
    position: absolute;
    right: 0;
    z-index: 1;
    width: 100vw;
    max-width: 1440px;

    &_fst{
        top: 45.347vw;
    }
    &_scd{
        top: 147.2916vw;
    }

    @media (min-width: 1440px){
        &_fst{
            top: 653px;
        }
        &_scd{
            top: 2121px;
        }
    }
}
.page__background{
    background: linear-gradient(180deg, #8CD6FF 0%, #EFDADA 40%);
    width: 100%;
    height: 60vw;
    max-height: 660px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;

    @media (max-width: 665px){
        height: 80vw;
    }
}