.header{
    padding: 40px 0;
    position: relative;
    z-index: 15;

    @media (max-width: 991px){
        padding: 26px 0;
    }
    @media (max-width: 434px){
        padding: 15px 0;
    }
}
.header__container{
    max-width: 1300px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.burgermenu{
    max-width: 24px;

    img{
        width: 100%;
    }

    @media (min-width: 991px){
        display: none;        
    }
}
.header__logo{
    display: flex;
    align-items: center;

    &_icon{
        max-width: 92px;
        width: 100%;
    }
    &_text{
        max-width: 97px;
        width: 100%;
        margin-left: 8px;
    }

    @media (max-width: 768px){
        &_icon{
            max-width: 65px;
        }
    }
}
.header__nav.header-nav{
    @media (max-width: 991px){
        display: none;
    }
}
.header__wallet-btn{
    width: 175px;
    height: 48px;
    background: #73B8D5;
    border-radius: 9px;

    .gbutton__wrapper{
        border: 3px solid #1C2934;
        transform: translate(-3px, -4px);
    }

    .gbutton__text{  
        font-size: 22px;  
    }

    @media (max-width: 991px){
        display: none;
    }
}
.header__wallet-btn.gbutton:hover{
    .gbutton__wrapper{
        transform: translate(-1px, -2px);
    }
    .gbutton__glass{
        left: 50%;
    }
}

.header__wallet-btn img{
    position: absolute;
    z-index: 2;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 100%;

    &::after,
    &::before{
        content: "";

    }
}