@import "../../scss/mixins.scss";

$step991: 80;

.utilies{
    padding-top: 5.9vw;
    padding-bottom: 1.6vw;
    position: relative;
    z-index: 3;
    overflow: hidden;

    @media (min-width: 1440px){
        padding-top: 85px;
        padding-bottom: 23px;
    }
    @media (max-width: 768px){
        padding-top: 131px;
    }
}
.utilies__container{
    max-width: 1205px;

    display: flex;

    @media (max-width: 768px){
        flex-direction: column;
    }
}
.utilies__left{
    width: 425px;

    @media (max-width: 991px){
        width: 42.9vw;
    }
    @media (max-width: 768px){
        width: 100%;
        text-align: center;
    }
}
.utilies__right{
    flex: 1 1;

    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media (max-width: 768px){
        align-items: center;
    }
    @media (max-width: 768px){
        padding-top: 85px;
        margin-left: -32px;
    }
    @media (max-width: 565px){
        padding-top: 120px;
    }
}

.utilies__title{
    display: inline-block;
    text-transform: uppercase;

    @media (max-width: 991px){
        span{            
            font-size: 8.88vw;
        }
    }
    @media (max-width: 768px){
        span{
            font-size: 70px;
        }
    }
    @media (max-width: 565px){
        span{
            font-size: 14.5vw;
        }
    }
}
.utilies__scribble{
    max-width: 100%;
    width: 100%;
    transform: translateX(-25px);

    @media (max-width: 768px){
        display: none;
    }
}
.utilies__fish{
    margin-top: 65px;
    margin-left: auto;

    @media (max-width: 1061px){
        margin: 65px 0 0;
    }
    @media (max-width: 991px){
        margin-top: 6.55vw;
        width: 35.82vw;
    }
    @media (max-width: 768px){
        display: none;
    }
}
.utilies__wrapper{
    position: relative;
    display: inline-block;
}
.utilies__seaweed{
    max-width: 256px;
    position: relative;
    z-index: 2;

    @media (min-width: 769px) and (max-width: 991px){
        max-width: calcSize(256px, $step991);
    }
    @media (max-width: 565px){
        max-width: 42.3vw;
    }
}
.utilies__paper{
    position: relative;
    z-index: 3;
    max-width: 400px;
    margin-left: 27px;
    margin-top: -28px;
    background: #FFFFFF;
    border: 4px solid #000000;
    box-shadow: 7px 9px 0px #000000;
    border-radius: 17px;
    padding: 50px 33px 25px;

    @media (min-width: 769px) and (max-width: 991px){
        // padding: calcSize(50px, $step991) calcSize(33px, $step991) calcSize(25px, $step991);
        padding: 41px 27px 31px;
        max-width: calcSize(400px, $step991);
        margin-top: calcSize(-28px, $step991);
        border-radius: calcSize(17px, $step991);
        margin-left: calcSize(27px, $step991);
    }

    @media (max-width: 565px){
        max-width: 79.95vw;
        margin-top: -3.623vw;
        padding: 9.9vw 6.5vw 7.4vw;
    }
    @media (max-width: 359px){
        max-width: 87vw;
    }
}
.utilies__fish-right{
    display: none;
    position: absolute;
    right: -30px;
    top: -20px;
    z-index: 1;
    transform: scaleX(-1) translateY(-20%) rotate(-12deg);
    max-width: 240px;

    @media (max-width: 768px){
        display: block;
    }
    @media (max-width: 565px){
        top: -10.62vw;
        max-width: 58vw;
    }
}
.utilies__list{
    display: flex;
    flex-direction: column;
}
.utilies__item + .utilies__item{
    margin-top: 24px;

    @media (max-width: 565px){
        margin-top: 4.83vw;
    }
}
.utilies__item{
    display: flex;
    align-items: center;
}
.utilies__item-icon{
    flex-shrink: 0;
    width: 44px;
    height: 44px;
    border: 3px solid #000;
    border-radius: 50%;
    box-shadow: 2px 2px 0 #000;

    display: flex;
    align-items: center;
    justify-content: center;

    img{
        max-width: 34px;
    }

    @media (max-width: 565px){
        width: 36px;
        height: 36px;

        img{
            max-width: 29px;
        }
    }
}
.utilies__item-text{
    margin-left: 13px;

    font-family: "PlusJakartaText", sans-serif;
    font-size: 13px;
    line-height: 1.3;
    font-weight: 700;
    color: #000;

    @media (max-width: 565px){
        font-size: 11px;
    }
}

.utilies__item-btn{
    margin: 41px auto 0;
    white-space: nowrap;
    width: 174px;
    height: 41px;
    background: #8462F6;
    border-radius: 10px;

    .gbutton__wrapper{
        transform: translate(-3px, -4.5px);
        border: 1px solid #233356;
    }

    .gbutton__text{
        font-size: 20px;
        line-height: 2.6;
    }
    .gbutton__glass{
        left: 32px;
    }

    @media (max-width: 565px){
        margin-top: 30px;
    }
}
.utilies__posttext{
    display: flex;
    margin-top: 36px;
    max-width: 400px;
    margin-left: 28px;

    span{
        margin-left: 8px;

        font-family: "PlusJakartaText", sans-serif;
        font-size: 19px;
        line-height: 1.3;
        font-weight: 700;
        color: #fff;
    }

    &::before{
        flex-shrink: 0;
        content: "";
        display: block;
        width: 7px;
        height: 7px;
        background: #fff;
        border-radius: 50%;
        margin-top: 9px;
    }

    @media (min-width: 769px) and (max-width: 991px){
        max-width: calcSize(400px, $step991);

        span{            
            font-size: 16px;
        }
    }
    @media (max-width: 565px){
        margin-top: 30px;
        max-width: 79.95vw;
        
        span{            
            font-size: 16px;
        }
    }
    @media (max-width: 359px){
        max-width: 87vw;
    }
}